'use client';

import AppWrapper from "@/components/AppWrapper"
import PrivateRoute from "@/components/PrivateRoute"

import { useSelector, useDispatch } from "react-redux";
import { LogOutAPI } from "@/features/Authentication/authSlice";

export default function DashBoard()
{
    const {user , checked , status} = useSelector((state) => state.auth);

    const dispatch = useDispatch();

    const HandleLogOut = async()=>{
        await dispatch(LogOutAPI()).unwrap();

    }

    return(

        
        <AppWrapper>
            <PrivateRoute>

                <div className="min-h-screen bg-white text-black">
                    <h1 className="text-2xl">Hello, Dashboard! </h1>
                    <button onClick={() => HandleLogOut()} className="text-black">LogOut</button>
                </div>       
                
            </PrivateRoute>
        </AppWrapper>

    )
}